import React from 'react';

import Parachains from '../../data/parachains.jsx';


export default function CrowdloanList(data) {

  const raising = data['raise']
  const contrib = data['contributions']
  const parachain = Parachains.parachain;
  const marketshare = [{}];

  if(raising && contrib) {
    const market = raising.reduce(function(sum, current) {
        return parseInt(sum) + parseInt(current.raised);
    }, 0);
    const share = contrib.reduce(function(sum, current) {
      return parseInt(sum) + parseInt(current.total);
  }, 0)

    marketshare.market = market;
    marketshare.share  = share;
    marketshare.porcent = ((marketshare.share/marketshare.market)*100).toFixed(2);
  }  





  return (
    <>
      <div className="flex">
        <div className="box" style={{width:'100%', maxWidth:'825px', height:'337px'}}>
          <h3> Total Value Locked</h3>
        </div>
        <div className="box" style={{width:"100%", maxWidth:'375px', marginLeft:'auto', textAlign:'center'}}>
          <h3>Parallel Share of Total Market</h3>
          <svg className="score"  viewBox="-25 -25 400 400">
            <circle className="score-empty" cx="175" cy="175" r="175" strokeWidth="25" fill="none"></circle>
            <circle className="js-circle score-circle" transform="rotate(-90 175 175)" cx="175" cy="175" r="175" strokeDasharray="1100" 
              strokeDashoffset={ !marketshare.market ? 0 : (1100 * (100 - marketshare.porcent)) / 100} strokeWidth="25" fill="none"></circle>
            <text className="js-text score-text" x="50%" y="45%" dx="-25" textAnchor="middle">
              { !marketshare.porcent ? 'calculating' : marketshare.porcent + '%'}
            </text>
            <text className="js-text score-text small" x="50%" y="55%" dx="-25" textAnchor="middle">
            { !marketshare.market ? 'calculating' : 
              (Math.abs(Number(marketshare.share/10000000000)) / 1.0e+6).toFixed(2) + 'm/' + (Math.abs(Number(marketshare.market/10000000000)) / 1.0e+6).toFixed(2) + 'm'  } 
            </text>
          </svg>      
        </div>  
      </div>
      <div className="box full">
        <h3>Crowdloan by Projects</h3>
        { !raising ? 'Loading...' : 
          raising.sort((a, b) => b.raised - a.raised).filter(data => data.paraId !== "2017").map((index) => {
            const id = index.paraId,
                  r  = index.raised;

            return (

              <div key={id}>
                <div className="flex">
                  
                  {!parachain ? 'Loading...' : parachain.filter(i => i.paraid === id).map(icon => (
                    <div key={icon} className="icon">
                      <img src={icon.icon} alt={icon.name} />
                    </div>
                  ))}    

                  {!contrib ? 'Loading...' : contrib.filter(g => g.paraId === id).map(graph => (
                    <>

                      <div className="meter" style={{width: ((r/marketshare.market)*100) + '%'}} >
                        <span style={{width: (graph.total/r)*100 + '%'}}></span>
                      </div>

                      <div>
                        {
                          (Math.abs(Number(r/10000000000)) / 1.0e+6).toFixed(2) + 'M'} | { ((graph.total/r)*100).toFixed(2) + '%' 
                        }
                      </div>
                    </>
                  ))}
                  
                </div>
              </div>
              
            )
         } )
        }
      </div>

  
    </>
  );
}